import React,{useState,useEffect} from'react'
import './index.css'
import fruit from './assets/fruit.png'
import jump from './assets/jump.png'
import { CozeAPI } from '@coze/api';
import './index.css'
import Loading from './component/loading'
const Contact = ()=>{
  const [inputValue, setInputValue] = useState('');
  const [transcript, setTranscript] = useState('');
  const [messages, setMessages] = useState([]);
  const [isLoading, setIsLoading] = useState(false)
  const [lastSectionId, setLastSectionId] = useState(localStorage.getItem('lastSectionId') || '')

  useEffect(()=>{
    console.log('isWeekSuggest',localStorage.getItem('isWeekSuggest'),localStorage.getItem('isWeekSuggest')==='true')
 

      console.log('dialogue',localStorage.getItem('dialogue'))
    if(localStorage.getItem('dialogue')){
      // dialogue=[...dialogue,{
      //   text:''
      // }]
      const data = JSON.parse(localStorage.getItem('dialogue'))
      console.log('后面饿messages',messages)
      messages.push(  {
        text: data.output,
        side: 'left',
      },)
      setMessages([
        ...messages
      ])
    }

    if(localStorage.getItem('isWeekSuggest')==='true'){
      // 在数组开头添加新消息
      setMessages([
        {
          text: '根据这一周我的情绪给我建议',
          side: 'right',
          avatar: fruit
        },
        ...messages // 保留原数组中的其他消息
      ])
    }

    return ()=>{
      localStorage.setItem('dialogue',[])
      localStorage.setItem('isWeekSuggest',false)
      setMessages([])
    }
  },[])
  console.log('messages',messages)
  

  console.log('transcript',transcript)
  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };

  const handleBlur = (e) => {
    console.log('Input field blurred');
    console.log(e.target.value);
    // 将输入框的值更新到 transcript 状态
    setTranscript(e.target.value);
 }

 const handleClearInput = async() => {
  console.log('inputValue',inputValue)

 
  if(lastSectionId.length !==0){
    console.log('lastSectionId',lastSectionId);
    const text = "根据最近一周的情绪给我一些建议"
    if(inputValue.length ===0){
      setIsLoading(true)
      const apiClient = new CozeAPI({
        token: 'pat_jNLXvspUJbObByD7SrFP4RR1Z8r7MjFLepeoVnvqgYGHVqfu298oZJf7U0MdmfrA',
        baseURL: 'https://api.coze.cn',
        allowPersonalAccessTokenInBrowser: true
      });
      const res = await apiClient.workflows.runs.create({
        workflow_id: '7457190265737101323',
        bot_id: '7456070465418117154',
        parameters: {
        input: text
      },
      });
      console.log('寻求建议----res',res)
      localStorage.setItem('dialogue',res.data)
      localStorage.setItem('isWeekSuggest',true)
      location.href = '/contact'
      setIsLoading(false)

    }

  }
  if(lastSectionId.length === 0){
    fetch('https://api.coze.cn/v1/conversation/create', {
      method: 'POST',
      headers: {
        'Authorization': 'Bearer pat_jNLXvspUJbObByD7SrFP4RR1Z8r7MjFLepeoVnvqgYGHVqfu298oZJf7U0MdmfrA',
        'Content-Type': 'application/json'
      }
    })
    .then(response => response.json()) // 将响应解析为JSON
    .then(({data}) => {
      console.log(data);
     
      if (data.last_section_id) {
        localStorage.setItem('lastSectionId', data.last_section_id);
        setLastSectionId(data.last_section_id||'')
        console.log('lastSectionId stored in localStorage:', data.last_section_id);
      } else {
        console.error('lastSectionId not found in response');
      }
    })
    .catch(error => {
      console.error('Error:', error);
    });
  }
  setInputValue('');
};

  return (
    <>
     {isLoading?<Loading/>: <div className="dialogue-container">
    {messages.map((message, index) => (<div key={index} className={`message-container ${message.side}`}>
      {message.avatar &&<span  className='avatar'>
        <img src={message.avatar} alt="Avatar"  />
      </span> }
      <div key={index} className={`message message-${message.side}`}>
        {message.text}
      </div>
    </div>
     
    ))}

    <div className='talk-container'>
      <input className='contact-talk' placeholder='AI 建议'   value={inputValue}   onChange={handleInputChange} onBlur={handleBlur} type="text" />
      <img className='talk-img' onClick={handleClearInput} src={jump} alt=''/>
    </div>
  </div>}
    </>
   
  )
     

}

export default Contact