import React, { useState } from 'react';
import './index.css';
import jump from './assets/jump.png';
import sunImg from './assets/sun.png';
import leafImg from './assets/leaf.png';
import limbImg from './assets/limb.png';
import fruitImg from './assets/fruit.png';
import boleImg from './assets/bole.png';
import soilImg from './assets/soil.png';
import rhizome from './assets/rhizome.png';
import rainstorm  from './assets/rainstorm.png'
import Loading from './component/loading';
import { CozeAPI } from '@coze/api';
const Classify = () => {
  const [inputValue, setInputValue] = useState('');
  const [activeIndex, setActiveIndex] = useState(0);
  const [lastSectionId, setLastSectionId] = useState(localStorage.getItem('lastSectionId') || '');
  const [isLoading, setIsLoading] = useState(false)
  const handleIconClick = (index) => {
    setActiveIndex(index);
  };

  const icons = [
    {id:0,src:rainstorm,count:1},
    { id: 1, src: sunImg,count:3 },
    { id: 2, src: leafImg ,count:2},
    { id: 3, src: limbImg ,count:4},
    { id: 4, src: fruitImg,count:2 },
    { id: 5, src: boleImg ,count:1},
    { id: 6, src: soilImg ,count:3},
    { id: 7, src: rhizome ,count:5},
  ];
  const labels = [
    {
      name: '愤怒',
      count: 3,
      texts: [
        { content: '今天吃饭吃到“蛋白质”，yue，难受', date: '10月01日' },
        { content: '我刚跟人吵架了，人太坏了，气死我       ', date: '10月02日' },
        { content: '今天开锁的师傅坑了我好多钱，我要分辨坏人       ', date: '10月03日' },
      ],
    },
    {
      name: '内疚',
      count: 2,
      texts: [
        { content: '也许，我也有一点不好，我对不起她们。', date: '10月04日' },
        { content: '我今天有些逃避，逃避别人的期望，我不好。我也想变好，可是我做不到，我对不起我自己。我对不起我自己，我对不起我自己', date: '10月05日' },
      ],
    }
  ];
 

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
  };

  const handleBlur = (e) => {
    console.log('Input field blurred');
    console.log(e.target.value);
  };

  const handleClearInput =async () => {
    if(lastSectionId.length !==0){
      console.log('lastSectionId',lastSectionId);
      const text = "根据最近一周的情绪给我一些建议"
      if(inputValue.length ===0){
        setIsLoading(true)
        const apiClient = new CozeAPI({
          token: 'pat_jNLXvspUJbObByD7SrFP4RR1Z8r7MjFLepeoVnvqgYGHVqfu298oZJf7U0MdmfrA',
          baseURL: 'https://api.coze.cn',
          allowPersonalAccessTokenInBrowser: true
        });
        const res = await apiClient.workflows.runs.create({
          workflow_id: '7457190265737101323',
          bot_id: '7456070465418117154',
          parameters: {
            input: text
        },
        });
        console.log('寻求建议----res',res)
        localStorage.setItem('dialogue',res.data)
        localStorage.setItem('isWeekSuggest',true)
        location.href = '/contact'
        setIsLoading(false)
  
      }
  
    }
    if(lastSectionId.length === 0){
      fetch('https://api.coze.cn/v1/conversation/create', {
        method: 'POST',
        headers: {
          'Authorization': 'Bearer pat_jNLXvspUJbObByD7SrFP4RR1Z8r7MjFLepeoVnvqgYGHVqfu298oZJf7U0MdmfrA',
          'Content-Type': 'application/json'
        }
      })
      .then(response => response.json()) // 将响应解析为JSON
      .then(({data}) => {
        console.log(data);
       
        if (data.last_section_id) {
          localStorage.setItem('lastSectionId', data.last_section_id);
          setLastSectionId(data.last_section_id||'')
          console.log('lastSectionId stored in localStorage:', data.last_section_id);
        } else {
          console.error('lastSectionId not found in response');
        }
      })
      .catch(error => {
        console.error('Error:', error);
      });
    }
    setInputValue('');
  };

  return (
    <>
    {isLoading?<Loading/>: <div className="dialogue-container">
      <div className="icon-slider">
        <div className="icon-container">
          {icons.map((icon, index) => (
            <div className='icon-wrapper'>
              <span  className={`icon-bg ${index === activeIndex ? 'active' : ''}`}>
                <img
                 key={icon.id}
                 src={icon.src}
                 alt={`Icon ${icon.id}`}
                 className={`icon ${index === activeIndex ? 'active' : ''}`}
                 onClick={() => handleIconClick(index)}
                />
              </span>
              <span className='icon-count'>
                ({icon.count})
              </span>
            </div>
          ))}
        </div>
      </div>
      <div className="emotion-labels">
      {labels.map((label, index) => (
        <div className="emotion-label" key={index}>
          <span className="label-text">{label.name} ({label.count})</span>
          <ul className="label-texts">
            {label.texts.map((text, textIndex) => (
              <li className="label-text-item" key={textIndex}>
                <span className='label-text-item-content'> {text.content} </span>
                <span className='label-text-item-date'>
                {text.date}
                </span>
              </li>
            ))}
          </ul>
        </div>
      ))}
    </div>
      <div className='talk-container'>
        <input className='contact-talk' placeholder='AI 建议' value={inputValue} onChange={handleInputChange} onBlur={handleBlur} type="text" />
        <img className='talk-img' onClick={handleClearInput} src={jump} alt='' />
      </div>
    </div>}
    </>
   
  );
};

export default Classify;