import React,{useState} from 'react';
// import { useSpeechRecognition } from 'react-speech-kit';
import WaveChart from './component/wave-chart';
import WordCloud from './component/floating-words';
import './styles/index.scss';
import './index.css'
import Curve from './assets/ic_Emotional_Curve.png'
import jump from './assets/jump.png'
import { CalendarPicker } from 'antd-mobile';
import dayjs from 'dayjs';
// import {  Link } from 'wouter';
import './index.css'
import Layout from './layout';
import { CozeAPI } from '@coze/api';
import Loading from './component/loading'
const defaultRange = [
  dayjs().toDate(),
  dayjs().add(2, 'day').toDate(),
]

const words = [
  { text: "平静", top: "50%", left: "50%", fontSize: "28px", color: "#4dabf7" },
  { text: "骄傲", top: "30%", left: "60%", fontSize: "18px", color: "#6a4c93" },
  { text: "开心", top: "20%", left: "40%", fontSize: "22px", color: "#f6a81c" },
  { text: "勇敢", top: "70%", left: "30%", fontSize: "20px", color: "#2f9e44" },
  { text: "恐惧", top: "80%", left: "60%", fontSize: "16px", color: "#ff6b6b" },
  { text: "内疚", top: "60%", left: "80%", fontSize: "18px", color: "#4dabf7" },
  { text: "生气", top: "40%", left: "20%", fontSize: "24px", color: "#6a4c93" },
];
const Chart = () => {
  const [isActive,setIsActive] = useState(1);
  const [inputValue, setInputValue] = useState('');
  const [lastSectionId, setLastSectionId] = useState(localStorage.getItem('lastSectionId') || '');
  const [transcript, setTranscript] = useState('');
  const [visible, setVisible] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const handleBlur = (e) => {
    console.log('Input field blurred');
    console.log(e.target.value);
    // 将输入框的值更新到 transcript 状态
    setTranscript(e.target.value);
    console.log('transcript',transcript);
 }
 const handleInputChange = (e) => {
  setInputValue(e.target.value);
};

const handleClearInput = async() => {
  console.log('inputValue',inputValue)

  if(lastSectionId.length !==0){
    console.log('lastSectionId',lastSectionId);
    const text = "根据最近一周的情绪给我一些建议"
    if(inputValue.length ===0){
      setIsLoading(true)
      const apiClient = new CozeAPI({
        token: 'pat_jNLXvspUJbObByD7SrFP4RR1Z8r7MjFLepeoVnvqgYGHVqfu298oZJf7U0MdmfrA',
        baseURL: 'https://api.coze.cn',
        allowPersonalAccessTokenInBrowser: true
      });
      const res = await apiClient.workflows.runs.create({
        workflow_id: '7457190265737101323',
        bot_id: '7456070465418117154',
        parameters: {
          input: text
      },
      });
      console.log('寻求建议----res',res)
      localStorage.setItem('dialogue',res.data)
      localStorage.setItem('isWeekSuggest',true)
      location.href = '/contact'
      setIsLoading(false)

    }

  }
  if(lastSectionId.length === 0){
    fetch('https://api.coze.cn/v1/conversation/create', {
      method: 'POST',
      headers: {
        'Authorization': 'Bearer pat_jNLXvspUJbObByD7SrFP4RR1Z8r7MjFLepeoVnvqgYGHVqfu298oZJf7U0MdmfrA',
        'Content-Type': 'application/json'
      }
    })
    .then(response => response.json()) // 将响应解析为JSON
    .then(({data}) => {
      console.log(data);
     
      if (data.last_section_id) {
        localStorage.setItem('lastSectionId', data.last_section_id);
        setLastSectionId(data.last_section_id||'')
        console.log('lastSectionId stored in localStorage:', data.last_section_id);
      } else {
        console.error('lastSectionId not found in response');
      }
    })
    .catch(error => {
      console.error('Error:', error);
    });
  }
  setInputValue('');
};

return (
  <Layout>
    {isLoading?<Loading></Loading>:<div className='chart-container'>
    <div className='wave-chart-container'>
      <p className='title'>
        <img src={Curve} alt=''></img>
        情绪曲线
      </p>
      <WaveChart />
    </div>
  

   <WordCloud words={words} />
   {isActive === 3&&<CalendarPicker
          visible={visible}
          defaultValue={defaultRange}
          selectionMode='range'
          onClose={() => setVisible(false)}
          onMaskClick={() => setVisible(false)}
          onChange={val => {
            console.log(val)
          }}
        />} 
   <div className='tree-item-button'>
        <p 
        className={isActive === 1?'activity':'default'}
        onClick={() => setIsActive(1)}>今日</p>
        <p
          className={isActive === 2?'activity':'default'}
          onClick={() => setIsActive(2)}>本周</p>
        <p className={isActive === 3?'activity':'default'} onClick={() =>{
          setIsActive(3)
          setVisible(true)
        } }>自定义</p>
       
      </div>
        <input className='talk custom-input' placeholder='AI 建议'   value={inputValue}   onChange={handleInputChange} onBlur={handleBlur} type="text" />
        <img className='talk-img' onClick={handleClearInput} src={jump} alt=''/>
  </div>}
 
  </Layout>
 
);

}
export default Chart;